import React from 'react'
import {Card,Col,Row,CardGroup} from "react-bootstrap"
import {Link} from "react-router-dom"
function BoysCategory() {
    return (
        <>
  <h2 className="heading"></h2>
  <div className='back-to-result'>
  <Link to= "/">Back to result</Link>
</div>
        <CardGroup className="card-group">
        <Card className="card">
          <Card.Img variant="top" src="./images/boys/jackets/j-1.jpg" />
          <Card.Body className="category-title">
            <Card.Title  className="category-title" as={Link} to="/boys/girlsdress"> </Card.Title>
            
          </Card.Body>
          </Card>
        <Card>
          <Card.Img variant="top" src="./images/boys/t-shirt/t-1.jpg"  />
          <Card.Body  className="category-title">
            <Card.Title className="category-title"as={Link} to="/boys/boystshirt"></Card.Title>
          </Card.Body>
          </Card>
        <Card>
          <Card.Img variant="top" src="./images/boys/sport/s-1.jpg"/>
          <Card.Body  className="category-title">
            <Card.Title className="category-title" as={Link} to="/boys/boyssport"> </Card.Title>
          </Card.Body>
          </Card>
        <Card>
          <Card.Img variant="top" src="./images/boys/pants/p-1.jpg" />
          <Card.Body  className="category-title">
            <Card.Title className="category-title" as={Link} to="/boys/boyspants"></Card.Title>
          </Card.Body>
          </Card>
        
      </CardGroup>
        </>
        
      )
}

export default BoysCategory