import React from 'react'
import "./category.css"
import {Card,Col,Row,CardGroup} from "react-bootstrap"
import {Link} from "react-router-dom"

  // const cardInfo=[
  //   {image:"./images/category/girl.jpg",title:"  ",link:"/girls"},
  //   {image:"./images/category/boy.jpg",title:"  ",link:"/boys"},
  //   {image:"./images/category/baby.jpg",title:"  ",link:"/baby"},
  //   {image:"./images/category/toy.jpg",title:"",link:"/toys"}
  // ];


  
  function Category() {
    return (
      <>
<h2 className="heading">   </h2>
      <CardGroup className="card-group">
      <Card className="card">
        <Card.Img variant="top" src="./images/category/girl.jpg" />
        <Card.Body className="category-title">
          <Card.Title  className="category-title" as={Link} to="/girls">  </Card.Title>
          
        </Card.Body>
        </Card>
      <Card>
        <Card.Img variant="top" src="./images/category/boy.jpg" />
        <Card.Body  className="category-title">
          <Card.Title className="category-title"as={Link} to="/boys">  </Card.Title>
        </Card.Body>
        </Card>
      <Card>
        <Card.Img variant="top" src="./images/category/baby.jpg" />
        <Card.Body  className="category-title">
          <Card.Title className="category-title" as={Link} to="/baby">  </Card.Title>
        </Card.Body>
        </Card>
      <Card>
        <Card.Img variant="top" src="./images/category/toy.jpg" />
        <Card.Body  className="category-title">
          <Card.Title className="category-title" as={Link} to="/toys"></Card.Title>
        </Card.Body>
        </Card>
      
    </CardGroup>
      </>
      
    )
  }
  
  export default Category
   
     
  